<template>
  <div class="pg-header">
    <div>
      <img class="logo-n" src="../../assets/mdLogo.png" alt="">
    </div>
    <div>
      <router-link to="/o/4-1"><img src="../../assets/icon/sound2-gray.png" class="sound-2"></router-link>
      <img src="../../assets/logout.png" class="logout" @click="userLogout">
    </div>
  </div>
</template>
<script>
  import { mapState, mapActions, mapMutations } from 'vuex'
  import { roles } from '../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {}
    },
    computed: {
      ...mapState([
        'user',
        'userLoginStatus',
      ])
    },
    watch: {
      user (d) {
        if (!d) {
          this.$router.push({name: 'signin'})
        }
      },
      userLoginStatus (d) {
        if (d) {
          this.$message({
            type: 'warning',
            message: '登陆已过期，请重新登陆'
          })
          this.$router.push({name: 'signin'})
        }
      }
    },
    methods: {
      ...mapMutations([
        'mut_user',
        'mut_departmentId',
      ]),
      ...mapActions([
        'ac_logout'
      ]),
      ...mapActions('message', [
        'ac_msgReceive',
      ]),
      userLogout () {
        this.ac_logout()
      }
    },
    components: {},
    beforeCreate () {},
    created () {
      if (sessionStorage.user) {
        this.mut_user(JSON.parse(sessionStorage.user))
      } else {
        this.$router.push({name: 'signin'})
      }
      // 下面是解决非管理员登录后刷新state中departmentId数据丢失问题
      let isAdmin = this.user.roles.some(function (role) { // todo 测试普通人员
        return role.id === roles.admin
      })
      if (!isAdmin) {
        this.mut_departmentId(this.user.job.department.id)
      }

      this.ac_msgReceive()
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../styles/var";

  .pg-header {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $color-white;
    .logo-n {
      margin-left: 25px;
      /*cursor: pointer;*/
    }
    .sound-2 {
      margin-right: 30px;
      position: relative;
      top: -2px;
      cursor: pointer;
    }
    .sound {
      margin-right: 30px;
      cursor: pointer;
    }
    .logout {
      margin-right: 50px;
      cursor: pointer;
    }
  }


</style>



