<template>
  <div class="page">
    <el-container>
      <el-header style="padding: 0" height="80px">
        <pg-header></pg-header>
      </el-header>
      <el-container>
        <el-aside width="270px" style="background-color: #fff;border-top: 3px solid #EEEEEE;overflow-x: hidden">
          <left-menu></left-menu>
        </el-aside>
        <el-main style="padding-right: 0">
          <pg-content></pg-content>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
  import pgHeader from './componentsBody/pgHeader.vue'
  import leftMenu from './componentsBody/leftMenuBar.vue'
  import pgContent from './componentsBody/content.vue'
  import ElHeader from '../../node_modules/element-ui/packages/header/src/main.vue'

  export default {
    name: 'body',
    props: {},
    data () {
      return {}
    },
    computed: {},
    methods: {},
    components: {
      ElHeader,
      pgHeader,
      leftMenu,
      pgContent,
    },
    beforeCreate () {},
    created () {},
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  .page {
    min-height: 870px;
  }
</style>



