<template xmlns="http://www.w3.org/1999/html">
  <div style="margin-top: 2px;">
    <div class="account">
      <img class="head-img" :src="serverBaseUrlCpt + user.headPath" alt="">
      <div class="box">
        <p class="row"><span class="name">{{user.name}}</span>
          <span class="pos pos-1" v-if="user.roles.some(function(role) {
            return role.id === rolesCpt.principal
          })">责</span>
          <span class="pos pos-2" v-if="user.roles.some(function(role) {
            return role.id === rolesCpt.reExam
          })">复</span>
          <span class="pos pos-3" v-if="user.roles.some(function(role) {
            return role.id === rolesCpt.admin
          })">管</span></p>
        <p class="row"><span class="pos-name">{{user.job.name}}</span></p>
      </div>
    </div>
    <el-menu :default-openeds="showLevel"
             :router="true">
      <el-submenu index="1" :class="{'active-bg': level1 === 1, 'point-events': level1 === 1}">
        <template slot="title">
          <div class="gray menu-title" :class="{white: level1 === 1}">
            <i class="sprites icon-people-white" v-if="level1 === 1"></i>
            <i class="sprites icon-people-gray" v-else></i>
            <span>人事管理</span>
          </div>
        </template>
        <el-menu-item-group :class="{'active-bg': level1 === 1}">
          <!--<template slot="title">分组一</template>-->
          <el-menu-item index="1-1" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '1-1', white: level1 === 1}">
            &nbsp;&nbsp;&nbsp;<span v-if="level1 === 1">&nbsp;&nbsp;&nbsp;</span>部门管理
          </el-menu-item>
          <el-menu-item index="1-2" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '1-2', white: level1 === 1}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 1">&nbsp;&nbsp;&nbsp;</span>岗位管理
          </el-menu-item>
          <el-menu-item index="1-3" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '1-3', white: level1 === 1}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 1">&nbsp;&nbsp;&nbsp;</span>人员管理
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="2"
                  :class="{'active-bg': level1 === 2}"
                  v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin
      })">
        <template slot="title">
          <div class="gray menu-title" :class="{white: level1 === 2}">
            <i class="sprites icon-email-white" v-if="level1 === 2"></i>
            <i class="sprites icon-email-gray" v-else></i>
            <span>邮件过滤管理（公司）</span>
          </div>
        </template>
        <el-menu-item-group :class="{'active-bg': level1 === 2}">
          <!--<template slot="title">分组一</template>-->
          <el-menu-item index="2-1" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '2-1', white: level1 === 2}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 2">&nbsp;&nbsp;&nbsp;</span>收信设置
          </el-menu-item>
          <el-menu-item index="2-2" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '2-2', white: level1 === 2}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 2">&nbsp;&nbsp;&nbsp;</span>屏蔽简历管理
          </el-menu-item>
          <el-menu-item index="2-3" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '2-3', white: level1 === 2}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 2">&nbsp;&nbsp;&nbsp;</span>关键字管理
          </el-menu-item>
          <el-menu-item index="2-4" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '2-4', white: level1 === 2}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 2">&nbsp;&nbsp;&nbsp;</span>已处理邮件
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="3"
                  :class="{'active-bg': level1 === 3}"
                  v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin || role.id === rolesCpt.principal || role.id === rolesCpt.reExam
                  })">
        <template slot="title">
          <div class="gray menu-title" :class="{white: level1 === 3}">
            <i class="sprites icon-dep-white" v-if="level1 === 3"></i>
            <i class="sprites icon-dep-gray" v-else></i>
            <span>部门招聘管理</span>
          </div>
        </template>
        <el-menu-item-group :class="{'active-bg': level1 === 3}">
          <!--<template slot="title">分组一</active-item-bgtemplate>-->
          <el-menu-item index="3-1" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '3-1', white: level1 === 3}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 3">&nbsp;&nbsp;&nbsp;</span>邮件过滤系统（部门）
          </el-menu-item>
          <el-menu-item index="3-2" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '3-2', white: level1 === 3}"
                        v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin || role.id === rolesCpt.principal
      })">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 3">&nbsp;&nbsp;&nbsp;</span>待处理列表
          </el-menu-item>
          <el-menu-item index="3-3" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '3-3', white: level1 === 3}"
                        v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin || role.id === rolesCpt.principal
      })">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 3">&nbsp;&nbsp;&nbsp;</span>初试列表
          </el-menu-item>
          <el-menu-item index="3-4" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '3-4', white: level1 === 3}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 3">&nbsp;&nbsp;&nbsp;</span>复试列表
          </el-menu-item>
          <el-menu-item index="3-5" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '3-5', white: level1 === 3}"
                        v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin || role.id === rolesCpt.principal
      })">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 3">&nbsp;&nbsp;&nbsp;</span>入职列表
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="4" :class="{'active-bg': level1 === 4}">
        <template slot="title">
          <div class="gray menu-title" :class="{white: level1 === 4}">
            <i class="sprites icon-sound2-white" v-if="level1 === 4"></i>
            <i class="sprites icon-sound2-gray" v-else></i>
            <span>消息</span>
          </div>
        </template>
        <el-menu-item-group :class="{'active-bg': level1 === 4}">
          <!--<template slot="title">分组一</template>-->
          <el-menu-item index="4-1" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '4-1', white: level1 === 4}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 4">&nbsp;&nbsp;&nbsp;</span>招聘消息
          </el-menu-item>
          <el-menu-item index="4-2" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '4-2', white: level1 === 4}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 4">&nbsp;&nbsp;&nbsp;</span>系统消息
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>

      <el-submenu index="5"
                  :class="{'active-bg': level1 === 5}"
                  v-if="user.roles.some(function(role) {
        return role.id === rolesCpt.admin
      })">
        <template slot="title">
          <div class="gray menu-title" :class="{white: level1 === 5}">
            <i class="sprites icon-safe-white" v-if="level1 === 5"></i>
            <i class="sprites icon-safe-gray" v-else></i>
            <span>可靠性保障</span>
          </div>
        </template>
        <el-menu-item-group :class="{'active-bg': level1 === 5}">
          <!--<template slot="title">分组一</template>-->
          <el-menu-item index="5-1" class="gray menu-item"
                        :class="{'active-item-bg': level2 === '5-1', white: level1 === 5}">&nbsp;&nbsp;&nbsp;<span v-if="level1 === 5">&nbsp;&nbsp;&nbsp;</span>邮件转发失败警报
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  import { roles, serverBaseUrl } from '../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        level1: 1,
        level2: '1-1',
        showLevel: ['1']
      }
    },
    computed: {
      ...mapState([
        'user'
      ]),
      rolesCpt () {
        return roles
      },
      serverBaseUrlCpt () {
        return serverBaseUrl
      }
    },
    watch: {
      '$route.path' () {
        this.setMenuBg()
      }
    },
    methods: {
      menuItem (item) {
//        console.info(item)
      },
      setMenuBg () {
        switch (this.$route.path) {
          case '/o/1-1':
            this.level1 = 1
            this.level2 = '1-1'
            this.showLevel = ['1']
            break
          case '/o/1-2':
            this.level1 = 1
            this.level2 = '1-2'
            this.showLevel = ['1']
            break
          case '/o/1-3':
            this.level1 = 1
            this.level2 = '1-3'
            this.showLevel = ['1']
            break
          case '/o/2-1':
            this.level1 = 2
            this.level2 = '2-1'
            this.showLevel = ['2']
            break
          case '/o/2-2':
            this.level1 = 2
            this.level2 = '2-2'
            this.showLevel = ['2']
            break
          case '/o/2-3':
            this.level1 = 2
            this.level2 = '2-3'
            this.showLevel = ['2']
            break
          case '/o/2-4':
            this.level1 = 2
            this.level2 = '2-4'
            this.showLevel = ['2']
            break
          case '/o/3-1':
            this.level1 = 3
            this.level2 = '3-1'
            this.showLevel = ['3']
            break
          case '/o/3-2':
            this.level1 = 3
            this.level2 = '3-2'
            this.showLevel = ['3']
            break
          case '/o/3-3':
            this.level1 = 3
            this.level2 = '3-3'
            this.showLevel = ['3']
            break
          case '/o/3-4':
            this.level1 = 3
            this.level2 = '3-4'
            this.showLevel = ['3']
            break
          case '/o/3-5':
            this.level1 = 3
            this.level2 = '3-5'
            this.showLevel = ['3']
            break
          case '/o/4-1':
            this.level1 = 4
            this.level2 = '4-1'
            this.showLevel = ['4']
            break
          case '/o/4-2':
            this.level1 = 4
            this.level2 = '4-2'
            this.showLevel = ['4']
            break
          case '/o/5-1':
            this.level1 = 5
            this.level2 = '5-1'
            this.showLevel = ['5']
            break

          default:
        }
      }
    },
    components: {},
    beforeCreate () {},
    created () {
      this.setMenuBg()
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../styles/sprites/sprites.css";
  @import "../../styles/var";

  .account {
    background-color: $color-white;
    display: flex;
    align-items: center;
    height: 86px;
    .head-img {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      margin-left: 5px;
      margin-right: 10px;
    }
    .row {
      display: flex;
      align-items: center;
    }
    .name {
      font-size: 20px;
      font-weight: bold;
      margin-right: 10px;
    }
    .pos {
      padding: 1px 4px;
      border-radius: 3px;
      color: #fff;
      font-size: 12px;
      display: inline-block;
      margin-right: 5px;
    }
    .pos-1 {
      background-color: #EB6941;
    }
    .pos-2 {
      background-color: #EFB33C;
    }
    .pos-3 {
      background-color: #5AD1D1;
    }
    .pos-name {
      color: #64A5E7;
      border: 1px solid #64A5E7;
      padding: 3px 10px;
      border-radius: 16px;
      font-size: 12px;
      margin-top: 2px;
    }
  }

  .sprites {
    position: relative;
    top: -3px;
    margin-right: 10px;
  }

  .active-bg {
    background-color: #64A5E7;
    color: #fff;
    .active-item-bg {
      background-color: #4294E7;
      color: #fff;
    }
  }
  .point-events {
    /*pointer-events: none;*/

  }

  .white {
    color: #fff !important;
    background-color: #64A5E7;
    width: 150%;
    position: relative;
    left: -20px;
    padding-left: 20px;
  }

  .gray {
    color: #999;
  }

  .menu-title {
    font-weight: bold;
  }

  .menu-item {
    /*padding-left: 20px;*/
  }
  .el-submenu__title:hover {
    pointer-events: none;
  }
</style>



